.App {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  flex-wrap: wrap;
}

a{
  color: rgb(11, 110, 153);
}

p{
  line-height: 2em;
}

p, h1, h2, h3, h4{
  color: #777;
}

h1{
  color: rgb(11, 110, 153);
}

h3, h4{
  font-weight: 700;
}

ul{
  list-style: none;
  padding: 0;
}

li{
  color: #777;
}

.header-box{
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 10em;
}

.first-section{
  display: flex;
  width: 500px;
  flex-direction: column;
  justify-content: start;
  align-items: start;
  margin: 3em 0 0 3em;
}

.intro-second-box{
  width: 300px;
}


.second-section{
  display: flex;
  gap: 4em;
  width: 500px;
  justify-content: center;
  align-items: center;
  margin: 3em 3em 0 0;
}


.contact li {
  text-align: left;
  padding: .5em .5em;
}

.img-box > img{
  border-radius: 50%;
}


.third-section{
  border-top: solid 6px #f4f4f4;
  margin-bottom: 8em;
  padding-top: 5em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3em;
}

.third-section li{
  text-align: left;
  padding: .5em .5em;
}

.third-box{
  margin-right: 5.5em;
}


.forth-section{
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2.5em;
  border-top: solid 6px #f4f4f4;
  margin-bottom: 8em;
  padding-top: 5em;
}

.project-box{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 7em;
}

.use-box li{
  text-align: left;
  padding: .5em .5em;
  color: #777;
}


.fifth-section{
  border-top: solid 6px #f4f4f4;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3em;
  margin-bottom: 8em;
  padding-top: 5em;
}

.last-section{
  border-top: solid 6px #f4f4f4;
  margin-bottom: 8em;
  padding-top: 5em;
}








